<template>
  <div class="container">
    <el-row v-if="isDesktop">
      <el-col :span="21">
        <el-input v-model="search" placeholder="搜索玩家（名称/备注/标签）"></el-input>
      </el-col>
      <el-col :span="3">
        <el-select v-model="selectedSeason" placeholder="选择赛季">
          <el-option
            v-for="season in seasons"
            :key="season"
            :label="season === '' ? '' : season"
            :value="season"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>
    <!-- 在移动设备上添加搜索按钮 -->
    <el-row v-if="!isDesktop">
      <el-col :span="20">
        <el-input v-model="search" placeholder="搜索玩家（名称/备注/标签）"></el-input>
      </el-col>
      <el-col :span="3">
        <el-button type="primary" @click="searchMobile">搜索</el-button>
      </el-col>
      <el-col :span="12">
        <el-select v-model="selectedSeason" placeholder="选择赛季">
          <el-option
            v-for="season in seasons"
            :key="season"
            :label="season === '' ? '' : season"
            :value="season"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>

    <!-- 根据设备类型显示 el-table 或 el-card -->
    <el-table
      v-if="isDesktop"
      :data="filteredData"
      border
      :default-sort="{ prop: 'Global Rank', order: sortOrder }"
    >
      <!-- el-table 列 -->
      <el-table-column label="赛季" prop="Season" width="110" sortable></el-table-column>
      <el-table-column label="地区" prop="Region" width="100"></el-table-column>
      <el-table-column
        label="全球排名"
        prop="Global Rank"
        width="110"
        sortable
      ></el-table-column>
      <el-table-column label="玩家名称" prop="Player Name" width="200">
        <template v-slot="scope">
          <div>
            {{ scope.row["Player Name"] }}
            <span v-if="scope.row.Remarks !== ''"> ({{ scope.row.Remarks }})</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="玩家标签" prop="Player Tag" width="120">
        <template v-slot="scope">
          <span v-html="formatPlayerTag(scope.row['Player Tag'])"></span>
        </template>
      </el-table-column>
      <el-table-column label="卡组信息" prop="Deck Info">
        <template v-slot="scope">
          <div class="card-images-container" v-if="scope.row['Deck Info']">
            <img
              v-for="card in scope.row['Deck Info'].split(',')"
              :key="card"
              :src="cardImages[card]"
              alt="card"
              class="card-image"
            />
          </div>
          <div v-else>No Deck Info Available</div>
        </template>
      </el-table-column>
    </el-table>

    <el-card
      class="elcard"
      v-else
      v-for="item in mobileFilteredData"
      :key="item['Global Rank']"
    >
      <div class="card-content">
        <div class="info">
          <div>{{ item.Season }}</div>
          <div>{{ item.Region }}</div>
          <div>
            <strong>{{ item["Global Rank"] }}名</strong>
          </div>
          <div>
            <strong>{{ item["Player Name"] }}</strong>
            <span v-if="item.Remarks !== ''"> ({{ item.Remarks }})</span>
          </div>
          <div v-html="formatPlayerTag(item['Player Tag'])"></div>
        </div>
        <div class="card-images-container" v-if="item['Deck Info']">
          <img
            v-for="card in item['Deck Info'].split(',')"
            :key="card"
            :src="cardImages[card]"
            alt="card"
            class="card-image"
          />
        </div>
        <div v-else>No Deck Info Available</div>
      </div>
    </el-card>
  </div>
</template>

<script>
import { cardImages } from "@/cardImages";
export default {
  data() {
    return {
      search: "",
      selectedSeason: "",
      seasons: [],
      cardImages: cardImages,
      data: [],
      sortOrder: "ascending",
      isDesktop: window.innerWidth > 768,
      mobileFilteredData: [], // Updated: Store mobile search results
    };
  },
  computed: {
    filteredData() {
      return this.data.filter((item) => {
        const includesSearch =
          item["Player Name"].toLowerCase().includes(this.search.toLowerCase()) ||
          item.Remarks.toLowerCase().includes(this.search.toLowerCase()) ||
          item["Player Tag"].toLowerCase().includes(this.search.toLowerCase());

        const matchesSeason =
          this.selectedSeason === "" || item["Season"] === this.selectedSeason;

        return includesSearch && matchesSeason;
      });
    },
  },
  mounted() {
    this.loadData();
    window.addEventListener("resize", this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    formatPlayerTag(tag) {
      // 去掉标签中的 '#'，然后构建超链接
      const formattedTag = tag.replace("#", "");
      return `<a href="https://royaleapi.com/player/${formattedTag}" target="_blank">#${formattedTag}</a>`;
    },
    loadData() {
      import("@/assets/cncr_data.json").then((json) => {
        this.data = json.data;
        this.seasons = this.extractSeasons(json.data);
        this.seasons.unshift("");
      });
    },
    extractSeasons(data) {
      return Array.from(new Set(data.map((item) => item.Season)));
    },
    handleResize() {
      this.isDesktop = window.innerWidth > 768;
    },
    searchMobile() {
      // Updated: Clear previous search results before updating mobileFilteredData
      this.mobileFilteredData = [];
      // Trigger search operation and update mobileFilteredData
      this.mobileFilteredData = this.data.filter((item) => {
        const includesSearch =
          item["Player Name"].toLowerCase().includes(this.search.toLowerCase()) ||
          item.Remarks.toLowerCase().includes(this.search.toLowerCase()) ||
          item["Player Tag"].toLowerCase().includes(this.search.toLowerCase());

        const matchesSeason =
          this.selectedSeason === "" || item["Season"] === this.selectedSeason;

        return includesSearch && matchesSeason;
      });
    },
  },
};
</script>
<style>
.container {
  padding: 20px;
}

.elcard {
  margin-top: 20px; /* 添加顶部边距，增加元素之间的间隔 */
}

.card-content {
  display: flex;
  flex-direction: row; /* 保持水平排列 */
  align-items: center; /* 居中对齐 */
  margin-bottom: 10px;
  justify-content: space-around; /* 在父容器中水平居中对齐，增加元素之间的间隔 */
}

.info {
  margin-top: 20px; /* 添加顶部边距，增加元素之间的间隔 */
  width: 150px;
}

.card-images-container {
  display: flex;
  flex-wrap: wrap; /* 允许卡牌图片换行 */
  width: 190px;
  justify-content: center; /* 水平居中对齐 */
}

.card-image {
  margin: 5px; /* 添加外边距，增加卡牌图片之间的间隔 */
  width: 30px;
  height: 36px;
}
</style>
